html,
body {
    max-height: 100vh;
    overflow-x: hidden;
    &::-webkit-scrollbar {
        width: 0;
    }
}
main {
    z-index: -10000;
    background-color: var(--color-white);
    padding-bottom: 20px;
}
.container{
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 15px;
}